@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap);
.sauce-list {
    height: 100%;
    padding: var(--dimen-medium-padding);
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(256px, 1fr));
    grid-template-rows: repeat(auto-fit, 256px);
    grid-gap: var(--dimen-small-padding);
    gap: var(--dimen-small-padding);
    box-sizing: border-box;
}

.sauce-container {
    padding: var(--dimen-medium-padding);
    background-color: var(--color-surface);
    color: var(--color-primary);
    border-radius: var(--dimen-surface-radius);
    border: 1px solid var(--color-primary);
}

.sauce-container .sauce-question {
    color: var(--color-primary-text);
}

.sauce-container .sauce-answer {
    color: var(--color-primary);
}
.editor-root {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
}

.editor {
    position: fixed;
    width: 30%;
    height: auto;
    top: 50%;
    left: 50%;
    padding: var(--dimen-medium-padding);
    transform: translate(-50%,-50%);
    background-color: var(--color-surface);
    border-radius: var(--dimen-surface-radius);
}

.editor-show { 
    visibility: visible; 
    opacity: 1;
    transition: visiblity 0s, opacity 0.5s linear;
}
.editor-hide { 
    visibility: hidden; 
    opacity: 0;
    transition: visibility 0s, opacity 0.5s linear;
}

.editor-form > button {
    width: 100%;
    margin-top: var(--dimen-medium-padding);
    padding: var(--dimen-small-padding);
    background-color: var(--color-primary);
    border-radius: var(--dimen-surface-radius);
    border: none;
    outline: none;
    color: var(--color-on-primary);
    font-weight: 500;
    font-size: 0.9em;
}

.editor-form > button:hover {
    cursor: pointer;
    background-color: var(--color-primary-variant);
}

.editor-form > input {
    width: 100%;
    margin-bottom: var(--dimen-small-padding);
    padding: var(--dimen-small-padding);
    background-color: var(--color-tertiary);
    border: 1px solid var(--color-primary-variant);
    outline: none;
    border-radius: var(--dimen-surface-radius);
    color: var(--color-primary-text);
    font-weight: 400;
    font-size: 1em;
}

.editor-form > input:hover {
    border: 1px solid var(--color-primary);
}
* {
    box-sizing: border-box;
}

body {
    background-color: var(--color-window-background);
}

.root-wrapper {
    display: flex;
    width: 100vw;
    height: 100vh;
}

.root-wrapper .container {
    overflow-x: hidden;
}

.root-wrapper .container:nth-of-type(1) { 
    display: flex;
    flex: 0.5 1;
    justify-content: center;
}
.root-wrapper .container:nth-of-type(2) { flex: 4 1; }

.button-wrapper {
    padding-top: 72px;
}

.button-wrapper > button {
    margin: auto;
}

.header {
    margin-top: var(--dimen-medium-padding);
    padding: var(--dimen-medium-padding);
    font-weight: 800;
    font-size: 2.2em;
    color: var(--color-primary-text);
}
:root {
	--color-primary:#3aafa9;
	--color-primary-variant: #2b7a7b;
	--color-on-primary: #ECEFF4;
	--color-secondary: #3aafa9;
	--color-secondary-variant: #2b7a7b;
	--color-on-secondary: #ffffff;
	--color-tertiary: #def2f1;
	--color-surface: #ECEFF4;
	--color-on-surface: var(--color-primary);
	--color-window-background: #feffff;
	--color-toolbar: var(--color-primary-variant);
	--color-primary-text: #17252a;
	--color-secondary-text: var(--color-primary);

	--dimen-icon-size: 16px;
	--dimen-medium-padding: 16px;
	--dimen-small-padding: 8px;
	--dimen-tiny-padding: 4px;
	--dimen-surface-radius: 4px;
}

body {
	margin: 0;
	font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #feffff;
	background-color: var(--color-window-background);
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

