button {
	border: none;
    outline: none;
    margin-right: 8px;
    margin-right: var(--dimen-small-padding);
	padding-top: 8px;
    padding-bottom: 4px;
    padding-left: 12px;
    padding-right: 12px;
    background-color: #3aafa9;
    background-color: var(--color-primary);
    color: #ECEFF4;
    color: var(--color-on-primary);
    border-radius: 4px;
    border-radius: var(--dimen-surface-radius);
    font-size: 1.2em;
}

button:hover {
	cursor: pointer;
	background-color: #2b7a7b;
	background-color: var(--color-primary-variant);
    transition: 0.5s ease-in-out;
}
